import axios from 'axios';

const BASE_URL = 'http://3.39.233.211:8080/api/foodDetails'; //  API 

export const fetchShopDetails = async (id) => {
  try {
    const response = await axios.get(`${BASE_URL}/${id}`);
    return response.data;
  } catch (error) {
    console.error('    :', error);
    throw error;
  }
};
